export default {
	name: 'business-order-list',
	props: {
		orderList: Array,
		isButton:''
	},
	data() {
		return {
			status: '',
			orderItem: false,
			buttonSubmit:'一键接单',
			buttonCancel:'确认取消'
		}
	},
	methods: {
		bindDetail(id){
			console.log(id)
			if(id == 3){
				uni.reLaunch({
					url:'../business-cancel/business-cancel?id' + id
				})
			} else{
				uni.navigateTo({
					url: '../../business/business-orderstatus/business-orderstatus?id=' + id
				})
			}
		},
		onCancel(){
			uni.reLaunch({
				url:'../../business/business-cancel/business-cancel'
			})
		}
	}
}
